import qs from 'qs';
import fetch from 'isomorphic-fetch';
import store from '@/store'
import toast from '@/plugin/toast'
import {platform} from "@/utils/platform";
import {bridge} from "@/utils/native/bridge";
import {local} from '@/utils/storage';

const defaultError = (msg) => {
    toast.show(msg)
};

const baseParams = () => {
    const {station_id, fake_station_id} = local.load('station_id');
    return {
        api_version: '8.22.0',
        app_client_id: 3,
        station_id: station_id || fake_station_id
    };
};

const mode = 'cors';
const credentials = 'include';
const defaultFormHeaders = {'Content-Type': 'application/x-www-form-urlencoded'};

const http = (method, url, body, loading, showError,noLogin) => {
    loading && (store.state.loading.isLoading = true);
    let headers = {};
    body = {...baseParams(), ...body};

    if (method === 'GET') {
        url = url + '?' + qs.stringify(body);
        body = undefined;
    }
    if (method === 'POST') {
        body = qs.stringify(body);
        headers = defaultFormHeaders;
    }

    return fetch(url, {
        method,
        headers,
        credentials,
        mode,
        body
    }).then((res) => {
        //res.ok => res.status(200-299)
        loading && (store.state.loading.isLoading = false);
        if (res.ok) {
            return res.json();
        } else {
            defaultError(res.msg);
            return Promise.reject('请求失败!');
        }
    }, () => {
        try {
            console.log('失败')
        } catch (e) {
            console.log('失败')
        }
    }).then((r) => {
        r = r || {};
        loading && (store.state.loading.isLoading = false);

        //记录服务器时间差
        if (r.server_time) {
            store.state.activity.differ_time = r.server_time - parseInt(new Date().getTime() / 1000)
        }
        if (r.success) {
            return Promise.resolve(r);
        } else {
            showError && defaultError(r.msg);
            if (r.code === 1111) {
                if (platform.isApp) {
                    bridge.login(({success}) => {
                        if (success) {
                            bridge.reloadWebView();
                        } else {
                            bridge.backWebView();
                        }
                    });
                }else if(!noLogin){
                  store.state.login.isShowLogin = true;
                }
            }
            return {error: r};
        }
    })
};

const postForm = (url, params = {}) => {
    const form = document.createElement('form');
    form.action = url;
    form.method = 'POST';

    Object.keys(params).forEach(key => {
        const input = document.createElement('input');
        input.type = 'hidden';
        input.name = key;
        input.value = params[key];
        form.appendChild(input);
    });

    document.body.appendChild(form);
    try {
        form.submit();
    } catch (ex) {
        /* eslint no-empty: 0 */
    }
    document.body.removeChild(form);
};

const httpFetch = {
    http,
    get: (url, params = {}, loading = true, showError = true,noLogin=false) => http('GET', url, params, loading, showError,noLogin),
    post: (url, params = {}, loading = true, showError = true,noLogin=false) => http('POST', url, params, loading, showError,noLogin),
    put: (url, params = {}, loading = true, showError = true,noLogin=false) => http('PUT', url, params, loading, showError,noLogin),
    delete: (url, params = {}, loading = true, showError = true,noLogin=false) => http('DELETE', url, params, loading, showError,noLogin),
    postForm
};

export default httpFetch;
