import API from '@/server/api'
import fetch from '@/server/http'
import {local} from '@/utils/storage'
import {LOGIN_WHITE, LOGIN_NO_CLOSE_WHITE, LOGIN_WHITE_NO_BOX} from '@/const/white'
import {platform} from "@/utils/platform";
import {bridge} from "@/utils/native/bridge";
import qs from 'qs';
import {changeQueryString} from "@/utils/tools"


const state = {
    isShowLogin: false,
    isCanClose: true,
    need_verify_code: 0,
    user_login: '',
    wx_applet_auth: 0,
    wx_auth: 0,
    user_info: null,
    openid: ''
};

const actions = {
    async setLoginCheck({commit, dispatch, rootState}, path) {
        const {data} = await fetch.get(API.userApi('checkLogin'));
        if (data) {
            const {user_login, wx_auth} = data;
            // 微信授权自动登录逻辑
            if (platform.isWeixin && !wx_auth) {
                const pageUrl = changeQueryString('random',parseInt(Math.random()*10)).toString();
                const query = qs.stringify({pageUrl});
                window.location.href = [API.weixinApi("auth"), '?', query].join('');
                return;
            }
            // 获取openid
            if (platform.isWeixin && !!wx_auth) {
                dispatch('setOpenId');
            }

            if (!user_login) {
                local.remove('USER_INFO');
                dispatch('filterPath', {path, rootState});
            } else {
                dispatch('setUserInfo')
            }
            commit('setLoginCheck', data);
            return data;
        }
    },
    async setUserInfo({commit}) {
        const {data} = await fetch.get(API.userApi('info'), {}, false);
        if (data) {
            local.save('USER_INFO', data);
            commit('setUserInfo', data);
            return data;
        }
    },
    filterPath(store, {path, rootState}) {
        const isLogin = store.state.user_login;
        if (LOGIN_WHITE.indexOf(path) >= 0) {
            if (!isLogin) {
                if (platform.isApp) {
                    //app在未登录前添加loading遮罩
                    if(rootState){
                        rootState.loading.isLoading = true;
                    }
                    bridge.login(({success}) => {
                        if(success) {
                            bridge.reloadWebView();
                        } else {
                            bridge.backWebView();
                        }
                    });
                } else if(LOGIN_WHITE_NO_BOX.indexOf(path) < 0){
                    //需要登录且需要默认弹框
                    store.commit('setShowLogin', true);
                }
            } else {
                store.commit('setShowLogin', false);
            }
        }
        store.state.isCanClose = !(LOGIN_NO_CLOSE_WHITE.indexOf(path) >= 0);
    },
    async setOpenId({ commit }) {
        const { data } = await fetch.get(API.weixinApi("getCurrentAuthOpenId"));
        if (data) {
            let { openid } = data;
            local.save('USER_OPEN_ID', { openid });
            commit('setOpenId',openid);
        }
    }
};

const mutations = {
    setLoginCheck(state, data) {
        const {user_login, wx_auth} = data;
        state.user_login = user_login;
        state.wx_auth = wx_auth;
    },
    setUserInfo(state, data) {
        state.user_info = data;
    },
    setShowLogin(state, data) {
        state.isShowLogin = data;
    },
    setOpenId(state, data) {
        state.openid = data;
    }
};

export default {
    namespaced: true,
    state,
    actions,
    mutations
}