import {platform} from '@/utils/platform';
import {android} from '@/utils/native/android';
import {ios} from '@/utils/native/ios';
import {event} from '@/utils/event';


/**
 * 通知原生type与事件的映射
 * @type {{}}
 */
const APP_TYPES_MAP = {
    'WEBVIEW_APPEAR': 'APP_E1',         //页面级显示
    'WEBVIEW_DISAPPEAR': 'APP_E2',      //页面级隐藏
    'TAP_RIGHTBUTTON': 'APP_E3',        //右上角按钮1事件
    'TAP_RIGHTSECONDBUTTON': 'APP_E4',  //右上角按钮2事件
    'APPLICATION_WILL_ENTER_FOREGROUND':'APP_E5',   //前台出现
    'TAP_BACKBUTTON': 'APP_E6',         //返回键
    'TAP_CLOSEBUTTON': 'APP_E7',        //关闭键
    'APPLICATION_DID_ENTER_BACKGROUND':'APP_E8',    //后台隐藏
};

/**
 * 注册全局方法，用于原生端单向通知JS
 * @param type
 * @param data
 * @private
 */
window['_dd_bridge_notify_'] = function (type, data) {
    type = type.toUpperCase();
    if (typeof data === 'string') {
        try {
            data = JSON.parse(data)
        } catch (e) {
            /* eslint no-empty:0 */
        }
    }
    const eventType = APP_TYPES_MAP[type];
    if (eventType) {
        let isNeed = event.publish(eventType, data);
        if(isNeed){
            return true;
        }
    }
};

const app = platform.isAndroid?android:ios;

let counter = 0;

const wrapCallbackName = (callback, methodName, isFixed, error = null) => {
    const callbackName = 'native_callback_v2_' + methodName + (isFixed ? '' : '_' + String(++counter));
    const timeoutId = setTimeout(() => {
        if (error) {
            error();
        }
    }, 2000);

    window[callbackName] = (res) => {
        clearTimeout(timeoutId);
        if (callback) {
            if (res) {
                const result = app.resolveResult(res);
                callback(result);
            } else {
                callback();
            }
        }
        if (!isFixed) {
            delete window[callbackName];
        }
    };

    return callbackName;
};

/**
 * 调用原生方法
 * @param methodName
 * @param params
 * @param isFixed
 * @param callback
 * @param error 原生方法2秒内未调用回调函数时时执行
 * @param encode
 */
const invoke = (methodName, params, isFixed, callback, error, encode) => {
    const callbackName = wrapCallbackName(callback, methodName, isFixed, error);

    app.invoke(methodName, params, callbackName, encode);
};

export const native = {invoke};
