<template>
    <i v-if="name" class="iconfont" :class="`icon-${name}`" :style="styleFont" @click="_click"></i>
</template>

<script>
    export default {
        name: 'font-icon',
        props: {
            //icon类名 去掉 icon-
            name: {
                type: String,
                default: ''
            },
            //字体大小
            size: {
                type: String,
                default: '32'
            },
            //字体颜色
            color: {
                type: String,
                default: '#444444'
            },
            //垂直居中
            align: {
                type: String,
                default: 'false'
            }
        },
        computed: {
            styleFont() {
                let cw = document.documentElement.clientWidth || 750;
                return {
                    fontSize: `${this.size*cw/750}px`,
                    color: this.color,
                    verticalAlign: this.align==='true'?'middle':'auto'
                };
            }
        },
        methods: {
            //透出原生click事件
            _click(){
                this.$emit('click');
            }
        }
    };
</script>
