/**
 * 本地存储
 * @param key
 */
import {isObject} from '@/utils/tools'

const localStorage = window.localStorage;
const BASE_TAG = 'DDMC_ACTIVITY';
const initKey = (key) => (`${BASE_TAG}_${key.toUpperCase()}`);
const store = {
    get: (key) => {
        return localStorage.getItem(initKey(key));
    },
    set: (key, val) => {
        return localStorage.setItem(initKey(key), val);
    },
    rem: (key) => {
        return localStorage.removeItem(initKey(key));
    }
};
const load = (key) => {
    let data = store.get(key);
    return data ? JSON.parse(data) : '';
};
const save = (key, data) => {
    store.set(key, JSON.stringify(data));
};
const update = (key, obj) => {
    let data = load(key);
    if(!data){
        store.set(key, JSON.stringify(obj));
        return;
    }
    if (isObject(data) && isObject(obj)) {
        let new_data = Object.assign(data, obj);
        store.set(key, JSON.stringify(new_data));
    } else {
        console.error('仅支持object类型更新: '+key);
    }
};
const remove = (key)=>{
    store.rem(key);
};

export const local = {
    load, save, update, remove
};
