import qs from 'qs';
import { platform } from '@/utils/platform';
import store from '@/store'
import { local } from '@/utils/storage'
import context from '@/server/servers';

/**
 * 来源
 * @type {string}
 */
let defaultChannel = '';    //渠道来源
let defaultFrom = '';       //客户端来源
let h5_source = '';         //第三方h5

if (platform.isApp) {
  defaultFrom = 'app';
} else if (platform.isWeixin) {
  defaultFrom = 'weixin';
} else {
  defaultFrom = 'web';
}

/**
 *  记录日志
 * params <String> | <Obejct>
 * --------------------------
 * 通用参数
 * appkey：站点Id，由统计后台生成。该项必选。
 * version：版本号，程序的版本号。该项可选。
 * time：请求时间。该项必选。
 * from:  来源，客户端、PC、H5、应用市场等等。该选项可选。
 * uid：用户标识，登陆用户设置为用户的Id如5292cec41944528014c40d56，游客设置为(a+1234567890+new Date().getTime()),a12345678901415861702606。该项必选。
 * sid:  一个连续行为的会话id，可以用于计算此步骤到下一个步骤的转化。该选项可选。
 * openid：微信用户唯一标识
 *
 * 行为参数
 * cid：要监控的目标的类型名称，通常是同一组目标的名字，比如”首页"、”商品页面"、”订单页"、"游戏"等等。该项必选。
 * aid：用户跟目标交互的行为，如”加入购物车"、”侧滑"、"下载"等等。该项必选。
 * label：事件的一些额外信息，通常可以是歌曲的名称、软件的名称、链接的名称等等。该项可选。
 * value：事件的一些数值信息，比如权重、时长、价格等等，在报表中可以看到其平均值等数据。该项可选。
 * @param params
 * 注意：入口页面日志需写在当前页watchUser()内
 */

let options = {
  appkey: '5292cec41944528014c40d58',
  version: '',
  referer: '',
  channel: defaultChannel,
  from: defaultFrom,
  uid: '',
  time: '',
  h5_source,
  openid: ''
};

//获取uid和openid
const getUid = () => {
  let uids = `a1234567890${new Date().getTime()}`;
  let user_local = local.load('USER_INFO');
  let openid_local = local.load('USER_OPEN_ID');
  let user_store = store.state.login.user_info;
  let openid_store = store.state.login.openid;
  let openid = openid_store || openid_local || '';
  let uid = user_store && user_store.id || user_local&& user_local.id || uids;
  return { uid, openid };
};

const collect = (params) => {
  if (!options.appkey || !params || Object.keys(params).length < 1) {
    return;
  }

  const queryData = {
    ...options,
    ...getUid(),
    time: new Date().getTime(),
    ...params
  };

  const url = context.LOGGER;
  new Image().src = url + qs.stringify(queryData)
};

export const logger = {collect, defaultChannel};
