const validateMobile = (mobile) => {
    const pattern = /^(10|13|14|15|16|17|18|19)\d{9}$/;
    if (pattern.test(mobile)) {
        return null;
    } else {
        return '手机号码不正确';
    }
};

const validateSmsCode = (code) => {
    const pattern = /^\d{4}$/;
    if (pattern.test(code)) {
        return null;
    } else {
        return '验证码格式不正确';
    }
};

const validateImgCode = (code) => {
    const pattern = /[a-zA-Z0-9]{4}$/;
    if (pattern.test(code)) {
        return null;
    } else {
        return '图形验证码格式不正确';
    }
};

export const validator = {validateMobile, validateSmsCode, validateImgCode};
