<template>
    <div id="app" class="app">
        <loading v-if="this.$store.state.loading.isLoading"/>
        <router-view v-wechat-title="$route.meta.title"></router-view>
        <login-box v-if="this.$store.state.login.isShowLogin"/>
    </div>
</template>

<script>
    import Loading from '@/components/common/Loading'
    import LoginBox from '@/components/common/LoginBox'
    import {platform} from "@/utils/platform";

    export default {
        name: "App",
        components: {
            Loading,
            LoginBox
        },
        created() {
            const $path = this.$path;
            //获取登录状态
            this.$store.dispatch('login/setLoginCheck', $path);

            this.$store.dispatch('location/getLocation', $path).then(()=>{
                this.removeIndexLoading();
            });
        },
        mounted() {
            this.focusOut();
        },
        methods: {
            //移除入口页loading
            removeIndexLoading() {
                const domIndexLoading = document.getElementById('i_loading');
                if (domIndexLoading) {
                    const parent = domIndexLoading.parentElement;
                    parent.removeChild(domIndexLoading);
                }
            },
            //软键盘收起
            focusOut() {
                if (platform.isAndroid) {
                    let originalHeight = document.documentElement.clientHeight || document.body.clientHeight;
                    window.onresize = () => {
                        let resizeHeight = document.documentElement.clientHeight || document.body.clientHeight;
                        if (resizeHeight >= originalHeight) {
                            window.scroll(0, 0);
                        }
                    };
                }
                if (platform.isIos) {
                    document.body.addEventListener('focusout', () => {
                        window.scroll(0, 0);
                    })
                }
            }
        }
    };
</script>
