/**
 * 获取值在数组中的区间索引
 * @param value
 * @returns {number}
 */
Array.prototype.arrayIndex = function (value) {
    let index = 0;
    this.filter((el, i) => {
        if (value > this[i] && value <= this[i + 1]) {
            index = i;
        }
    });
    return index;
};

/**
 * 获取数组中最大、最小值
 * @returns {number}
 */
Array.prototype.max = function () { return Math.max.apply({},this); };
Array.prototype.min = function () { return Math.min.apply({},this); };
