let pool = [], counter = 1;

const subscribe = (type, callback, appNo=null) => {
    pool.push({id: counter, type, callback, appNo});
    return counter++;
};

const unsubscribe = (id) => {
    pool = pool.filter(o => o.id !== id);
};

const publish = (type, args) => {
    let isNeed = null;
    pool.filter(o => o.type === type).forEach(o =>{
        //需要返回给app结果的事件
        isNeed = o.appNo;
        return o.callback && o.callback(args)
    });
    return isNeed;
};

export const event = {subscribe, unsubscribe, publish};
