import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter);

const routers = [
    {
        path: '/',
        meta: {
            title: '页面不见了'
        },
        component: '404'
    },
    {
        path: '/bill',
        name: 'Bill',
        meta: {
            title: '年度账单'
        },
        component: 'Bill'
    },
    {
        path: '/bill/detail',
        meta: {
            title: '叮咚买菜2018感谢有你'
        },
        component: 'BillDetail'
    },
    {
        path: '/bill/pack',
        name: 'BillPack',
        meta: {
            title: '叮咚买菜2018感谢有你'
        },
        component: 'BillPack'
    },
    {
        path: '/newTask/record',
        name: 'AwardRecord',
        meta: {
            title: '奖励记录'
        },
        component: 'NewTaskRecord'
    },
    {
        path: '/newTask',
        name: 'NewTask',
        meta: {
            title: '新手任务'
        },
        component: 'NewTask'
    },
    {
        path: '/vip',
        name: 'Vip',
        meta: {
            title: '绿卡'
        },
        component: 'Vip'
    },
    {
        path: '/vip/pay',
        name: 'VipPay',
        meta: {
            title: '开通绿卡'
        },
        component: 'VipPay'
    },
    {
        path: '/vip/payResult',
        name: 'VipPayResult',
        meta: {
            title: '支付结果'
        },
        component: 'VipPayResult'
    },
    {
        path: '/vip/friends',
        name: 'VipFriends',
        meta: {
            title: '亲友卡赠送'
        },
        component: 'VipFriends'
    },
     {
        path: '/vip/friendsGive',
        name: 'VipFriendsGive',
        meta: {
            title: '亲友卡领取'
        },
        component: 'VipFriendsGive'
    },
    {
        path: '/invite',
        name: 'Invite',
        component: 'Invite'
    },
    {
        path: '/coupon',
        name: 'Coupon',
        component: 'Coupon'
    },
    {
        path: '*',
        meta: {
            title: '页面不见了'
        },
        component: '404'
    },

];

const loadView = (name) => () => import(/* webpackChunkName: "[request]" */ `@/views/${name}`);
routers.map((r, i) => {
    routers[i].component = loadView(r.component);
});

export default new VueRouter({
    routes: routers,
    scrollBehavior(to, from, savedPosition) {
        if (savedPosition) {
            return savedPosition
        } else {
            return { x: 0, y: 0 }
        }
    }
    // mode: 'history'
})
