<template>
    <div class="login-mask">
        <div class="login-box" @click.stop>
            <div class="login-title">登录</div>
            <div class="login-hint">{{hint_text}}</div>
            <input class="input-mobile" type="number" v-model="mobile" placeholder="输入手机号码">
            <div class="login-img-code" v-if="need_verify_code">
                <input class="input-code" type="text" v-model="verify_code" placeholder="输入图形验证码">
                <div class="img-code-show" @click="eventChangeImg">
                    <img :src="verify_code_img" alt=""/>
                </div>
            </div>
            <div class="login-code">
                <input class="input-code" type="number" v-model="code" placeholder="输入验证码">
                <span :class="'btn-sms'+(isCanGetCode?' on':'')" @click="eventGetSms">{{time_text}}</span>
            </div>
            <div class="btn-submit" @click="eventSubmit">登录</div>
            <div class="btn-close" @click="eventCloseBox" v-if="isCanClose">
                <icon-font name="gouwuchedankuangguanbi" color="#ffffff" size="44"/>
            </div>
        </div>
    </div>
</template>

<script>
    import API from '@/server/api'
    import {validator} from '@/utils/validator'
    import {BILL_WHITE} from '@/const/white'
    import {local} from '@/utils/storage'
    import {changeQueryString} from '@/utils/tools'

    let timeDown = null;

    export default {
        components: {},
        data() {
            return {
                mobile: '',
                verify_code: '',
                code: '',
                need_verify_code: 0,

                time_max: 60,
                time_text: '获取验证码',
                isCanGetCode: false,
                hint_text: '',
                verify_code_img: API.userApi('verifyCode?' + Math.random()),
                isCanClose: this.$store.state.login.isCanClose
            }
        },
        watch: {
            mobile(val) {
                this.isCanGetCode = !validator.validateMobile(val);
                this.mobile = this.maxLength(val, 11);
            },
            verify_code(val) {
                this.verify_code = this.maxLength(val, 4);
            },
            code(val) {
                this.code = this.maxLength(val, 4);
            }
        },
        methods: {
            async requestCodeSms(cb = null, param={type:1}) {
                const mobile = this.mobile;
                const verify_code = this.verify_code;
                const _that = this;
                const {type} = param;
                const {data, error} = await this.$http.get(API.userApi('getSms'), {mobile, verify_code, type}, false, false);
                if (data) {
                    !!cb && cb()
                }
                if (error){
                    if(error.code === -1000){
                        const {need_verify_code} = error.data;
                        this.need_verify_code = need_verify_code;
                        if (need_verify_code) {
                            this.hint_text = error.msg
                        }
                    }
                    if (error.code === 1205){
                        this.$confirm({
                            title: '建议获取语音验证码',
                            content: '收不到验证码？可尝试语音验证码，请点击确定后注意接听电话',
                            success() {
                                _that.requestCodeSms(()=>{
                                    _that.countDown();
                                },{
                                    type: 3
                                });
                                _that.log({
                                    cid: 'login',
                                    aid: 'get_voice_code'
                                });
                            }
                        });
                    }else{
                        this.$toast(error.msg);
                    }
                }
            },
            async requestLogin() {
                //登录渠道
                let source = '';
                if (BILL_WHITE.indexOf(this.$path) >= 0) {
                    source = 'bills'
                }
                const param = {
                    mobile: this.mobile,
                    verify_code: this.verify_code,
                    code: this.code,
                    source
                };
                const {data} = await this.$http.get(API.userApi('qlogin'), param, false);
                if (data) {
                    const {id, is_new_user} = data;
                    local.save('USER_INFO', data);
                    this.$store.state.login.user_info = data;
                    this.log({
                        cid: 'login',
                        aid: 'login_success',
                        name: is_new_user ? 'is_new_user' : '',
                        uid: id,
                        source
                    });
                    this.winReload();
                }
            },
            winReload(){
                if(!location.reload){
                    //android小米个别机型跳转兼容
                    window.top.location.reload();
                    window.location.href = changeQueryString('time',new Date().getTime())
                }else{
                    window.location.reload();
                }
            },
            maxLength(str, num) {
                this.hint_text = '';
                return str.length > num ? String(str).slice(0, num) : str
            },
            eventCloseBox() {
                this.$store.state.login.isShowLogin = false;
            },
            eventGetSms() {
                let vMobile = validator.validateMobile(this.mobile);
                let vImg = null;
                if (this.need_verify_code) {
                    vImg = validator.validateImgCode(this.verify_code);
                }
                if (vMobile || vImg) {
                    this.hint_text = vImg || vMobile;
                    return;
                }

                if (this.isCanGetCode) {
                    this.requestCodeSms(()=>{
                        this.countDown();
                    });
                }
            },
            eventChangeImg() {
                this.verify_code_img = API.userApi('verifyCode?' + Math.random())
            },
            countDown() {
                this.isCanGetCode = false;
                this.time_text = `获取(${this.time_max})`;
                timeDown = window.setInterval(() => {
                    this.time_max--;
                    this.time_text = `获取(${this.time_max})`;
                    if (this.time_max < 0) {         //当倒计时小于0时清除定时器
                        window.clearInterval(timeDown);
                        this.time_text = '重新获取';
                        this.time_max = 60;
                        this.isCanGetCode = true;
                    }
                }, 1000)
            },
            eventSubmit() {
                let vMobile = validator.validateMobile(this.mobile);
                let vCode = validator.validateSmsCode(this.code);
                if (vMobile || vCode) {
                    this.hint_text = vMobile || vCode;
                    return false;
                } else {
                    this.hint_text = ''
                }

                this.requestLogin();
            },
            destroyed() {
                window.clearInterval(timeDown);
            }
        }
    }
</script>

