        }
        .btn-submit {
            background: linear-gradient(90deg, #24D74E 0%, #2EBA5A 100%);
            height: 80px;
            line-height: 78px;
            font-size: 40px;
            border-radius: 45px;
            width: 100%;
            color: $color_f;
            text-align: center;
            margin-top: 60px;
            font-weight: bold;
        }
        .btn-close {
            position: absolute;
