        }
        .btn-sms {
            font-size: 26px;
            font-weight: bold;
            @include ibm;
            padding: 17px 15px;
            float: right;
            background: $color_dc;
            color: $color_f;
            border-radius: 32px;
            text-align: right;
            &.on {
                background: $color_g;
