const resolveResult = (res) => {
    if (typeof res === 'string' && res) {
        const reg = /\r\n|\r|\n/g;
        res = res.replace(reg, '');
        let result = res;
        try {
            result = JSON.parse(res);
        } catch (e) {
            /* eslint no-empty:0 */
        }
        return result;
    } else {
        return res;
    }
};

/**
 * 安卓客户端接口
 * @param methodName
 * @param param
 * @param callbackName
 * @param encode
 * @private
 */
const invoke = (methodName, param = {}, callbackName, encode) => {
    if (callbackName) {
        param.callback = callbackName;
    }
    param.r = new Date().getTime();

    let strParam = JSON.stringify(param);
    if (encode) {
        strParam = encodeURIComponent(strParam);
    }
    try {
        window.html['notifyAndroid_' + methodName](strParam);
    } catch (e) {
        /* eslint no-empty:0 */
    }
};

export const android = {resolveResult, invoke};
