import {logger} from '@/utils/logger'
import store from '@/store'
import {openUrl} from '@/utils/open'
import {APP_EVENTS} from '@/const/event'
import {event} from '@/utils/event'
import {platform} from "@/utils/platform"
import {bridge} from "@/utils/native/bridge"
import {compVer} from '@/utils/tools'

export default {
    data(){
      return{
          baseStopScroll: false
      }
    },
    computed: {
        user_info() {
            return store.state.login.user_info || {};
        },
        user_login() {
            return store.state.login.user_login;
        },
        station_id_ready() {
            return store.state.location.ready;
        },
        openid() {
          return store.state.login.openid;
        }
    },
    watch: {
        user_info(user) {
            if (user) {
                //已获取用户信息钩子函数
                !!this.watchUser && this.watchUser(user);
            }
        },
        openid(openid) {
          openid&&!!this.watchOpenId && this.watchOpenId(openid);
        }
    },
    methods: {
        //日志函数
        log(param) {
            logger.collect(param)
        },
        //延迟执行
        waitInit(fun){
            !!fun && setTimeout(()=>{
                fun();
            }, 500);
        },
        //页面跳转
        openUrl(params){
            openUrl(params, this)
        },
        //监听App事件
        eventListenerApp(eve, fun, appNo){
            APP_EVENTS[eve] = event.subscribe(APP_EVENTS[eve], fun, appNo);
        },
        setTitle(title) {
            this.$route.meta.title = title;
            if(platform.isApp) {
                bridge.changeTitle(title)
            }
        },
        //阻止事件
        selfDefault(e){
            e.preventDefault();
        },
        //禁用滚动条
        stopScroll() {
            //ios8.x.x不支持禁用滚动
            if(platform.isIos && compVer(platform.version,'9.0.0')<0) return;
            this.baseStopScroll = true;
            document.body.style.overflow = 'hidden';
            document.body.addEventListener('touchmove', this.selfDefault, { passive: false });
        },
        //激活滚动条
        activeScroll() {
            if(this.baseStopScroll){
                document.body.style.overflow = '';
                document.body.removeEventListener('touchmove', this.selfDefault);
            }
        }
    },
    destroyed(){
        //若存在禁用操作，在页面销毁时自动激活滚动
        if(this.baseStopScroll){
            this.baseStopScroll = false;
            this.activeScroll();
        }
    }
}
