import {platform} from "@/utils/platform";
import {bridge} from "@/utils/native/bridge";
import context from "@/server/servers";
import qs from 'qs';

/**
 * 页面跳转
 * @param params
 * - path(跳转多类型) 默认: 项目内地址，maicai:买菜h5地址，ddxq:app地址
 * - query(跳转携带参数)
 * - replace(是否重置route history)
 * @param vm vue对象
 */
const openUrl = (params, vm) => {
    const protocol = window.location.protocol;
    if (!params) return;
    let {path, query, replace = false} = params;
    let routers = JSON.stringify(vm.$router.options.routes);
    let url = '';
    let urlQuery = query ? `?${qs.stringify(query)}` : '';

    if (path) {
        if (routers.indexOf(path) >= 0) {
            if(!replace){
                vm.$router.push({path, query});
            }else{
                vm.$router.replace({path, query});
            }
        } else if (path.indexOf('maicai:') >= 0) {
            url = `${protocol}${context.MAICAI}#${path.replace('maicai:','')}${urlQuery}`;
        } else if (path.indexOf('ddxq:') >= 0) {
            url = platform.isApp ? `${path}${urlQuery}` : '';
        } else {
            url = `${path}${urlQuery}`;
        }
    }

    if(!url) return;
    if (platform.isApp) {
        bridge.openNewWebView({web_url: url});
    } else {
        window.location.href = url;
    }
};

export {openUrl};
