import {platform} from '@/utils/platform'
import API from '@/server/api'
import fetch from '@/server/http'

const wx = window.wx;

let readyPromise = null;

/**
 * 获取验证签名
 */
const requestJsApi = async() => {
    const url = window.location.href.split('#/')[0];
    return fetch.get(API.weixinApi('weixinJsapi'), {url}, false);
};
const loadSign = () => requestJsApi().then(({data}) => {
    if (data) {
        return data;
    }
});

const wxConfig = (token) => {
    if (!token) {
        return;
    }

    const config = Object.assign({
        debug: false,
        jsApiList: [
            'getLocation',
            'onMenuShareAppMessage',
            'onMenuShareTimeline',
            'onMenuShareWeibo'
        ]
    }, token);
    wx.config(config);
    return new Promise((resolve, reject) => {
        wx.ready(() => {
            resolve();
        });
        wx.error((res) => {
            console.log('wx error:' ,JSON.stringify(res));
            reject();
        });
    });
};

const getLocation = () => {
    if (!platform.isWeixin) {
        return;
    }

    return readyPromise.then(() => {
        return new Promise((resolve) => {
            wx.getLocation({
                type: 'gcj02', // 火星坐标
                success(res) {
                    resolve({data: res});
                },
                fail(res) {
                    resolve({error: res});
                },
                cancel(res) {
                    resolve({error: res});
                }
            });
        });
    });
};

const setShareOption = (option) => {
    if (!platform.isWeixin) {
        return;
    }

    if (option.img_url) {
        option.imgUrl = option.img_url;
        delete option.img_url;
    }

    // 朋友圈分享信息
    readyPromise.then(() => {
        wx.onMenuShareTimeline(option);
        wx.onMenuShareAppMessage(option);
        wx.onMenuShareWeibo(option);
    });
};

const init = () => {
    if (!platform.isWeixin) {
        return;
    }

    readyPromise = loadSign().then(token => wxConfig(token)).catch(error => {
        return {error: error || 'weixin not ready'};
    });
};

init();

export const weixin = {getLocation, setShareOption};
