import router from '@/router'
import store from '@/store'
import {bridge} from '@/utils/native/bridge'
import {platform} from '@/utils/platform'
import {APP_EVENTS} from '@/const/event'
import {event} from '@/utils/event'

//过滤分享后连接
const checkShareUrl = () => {
    let href = window.location.href;
    if (href.indexOf('isappinstalled') >= 0 || href.indexOf('groupmessage') >= 0 || href.indexOf('timeline') >= 0 || href.indexOf('singlemessage') >= 0) {
        window.location.replace(window.location.origin + '/#' + href.split('#')[1])
    }
};

//路由守卫
router.beforeEach(({path, meta}, from, next) => {
    checkShareUrl();

    //初始化app按钮&事件
    if (platform.isApp) {
        bridge.setRightTopButton({});
        bridge.setNavigationBarStyle({ style: 0});
        bridge.changeTitle(meta.title);
        for (let key in APP_EVENTS) {
            if (key !== APP_EVENTS[key]) {
                event.unsubscribe(APP_EVENTS[key]);
                APP_EVENTS[key] = key;
            }
        }
    }

    //跳转时判断登录态， 判断是否要获取地址
    if (from.name) {
        store.dispatch('login/filterPath', {path});
        store.dispatch('location/getLocation', path);
    }

    next();
});

export const routerBefore = router;
