import context from '@/server/servers';
const protocol = window.location.protocol;
const baseUrl = (str) => `${protocol}${context.SERVER}${str}`;
const api7Url = (str) => `${protocol}${context.API7}${str}`;

/**
 * @param action {string}
 * @check 检测定位地址
 */
const userLocationApi = (action) => api7Url(`user/location/${action}/`);

/**
 * @param action {string}
 * @checkLogin 检测登录状态
 * @getSms 获取短信验证码
 * @verifyCode 获取图片验证码
 * @qlogin 登录
 * @logout 登出
 */
const userApi = (action) => baseUrl(`user/${action}`);

/**
 * @param action
 * @weixinJsapi 微信签名
 * @getCurrentAuthOpenId 获取openid
 * @auth 微信授权
 */
const weixinApi = (action) => baseUrl(`weixin/${action}`);

/**
 * @param action
 * @detailInfo 用户账单
 * @reward 领取优惠券
 * @getUserCouponInfo 获取券信息
 */
const userBill = (action) => baseUrl(`annualAccount/${action}`);

/**
 * 新手任务
 * @param action
 * @attend 活动详情
 * @taskReward 领取任务奖励 {task_id}
 * @growReward 领取满积分奖励 {task_id}
 * @rewardList 奖励历史记录
 */
const newTask = (action) => baseUrl(`novice/${action}`);

/**
 * @param action {string}
 * @card vip购买信息
 * @buy vip创建订单
 * @detail vip详情页
 * @product vip详情页
 * @payCheck vip支付结果
 * @receiveTicket vip获取优惠券
 * @friendsCardList vip亲友卡列表
 * @friendsCardSend vip亲友卡赠送
 */
const vipApi = (action) => baseUrl(`vip/${action}`);

/**
 * @param action {string}
 * @categoriesdetail vip分类商品
 */
const homeApi = (action) => baseUrl(`homeApi/${action}`);

/**
 * @param {*} action
 * @inviteH5 线下拉新
 * @newInvite 邀请码&兑换码
 * @info 领券活动信息
 * @attend 领券
 */
const eventApi = (action) => baseUrl(`event/${action}`);

const API = {
    userLocationApi,
    userApi,
    weixinApi,
    userBill,
    newTask,
    vipApi,
    homeApi,
    eventApi
};

export default API;
