const resolveResult = (res) => {
    let success = res['success'];
    if (typeof success !== 'boolean' && (success === 0 || success === 1)) {
        res.success = !!parseInt(res.success);
    }
    return res;
};

/**
 * IOS客户端接口
 * @param methodName
 * @param params
 * @param callbackName
 * @returns {Number}
 */
const invoke = (methodName, params = {}, callbackName) => {
    if (!methodName) {
        throw new Error('methodName required!');
    }

    //不能改为箭头函数
    const callbackFn = function () {
        window[callbackName].apply(window, arguments);
    };

    methodName = '__ios_call__' + methodName;

    const WVJBCallbackFn = bridge => bridge['callHandler'](methodName, params, callbackFn);

    const bridge = window['WebViewJavascriptBridge'];
    if (bridge) {
        return WVJBCallbackFn(bridge);
    }


    if (window.WVJBCallbacks) {
        return window.WVJBCallbacks.push(WVJBCallbackFn);
    }


    // window.WVJBCallbacks = [WVJBCallbackFn];
    window.WVJBCallbacks = [];
    const WVJBIframe = document.createElement('iframe');
    WVJBIframe.style.display = 'none';
    WVJBIframe.src = 'wvjbscheme://__BRIDGE_LOADED__';
    document.documentElement.appendChild(WVJBIframe);
    setTimeout(() => document.documentElement.removeChild(WVJBIframe), 0);
};

export const ios = {resolveResult, invoke};
