import API from '@/server/api';
import fetch from '@/server/http';
import {platform} from "@/utils/platform";
import {bridge} from "@/utils/native/bridge";
import {local} from '@/utils/storage';
import {LOCATION_WHITE} from '@/const/white';
import {weixin} from '@/utils/weixin';

const key = 'station_id';

const state = {
    ready: 0
};

const actions = {
    async getLocation({commit}, path) {
        if (LOCATION_WHITE.indexOf(path) >= 0) {
            commit('setReady', 0);

            if (platform.isApp) {
                //获取当前定位坐标
                bridge.getCurrentAddress({
                    business: 1,
                    callback: (res) => {
                        if (platform.isIos) {
                            if (res.success) {
                                if (res.data.address.station_id) {
                                    local.save(key, {station_id: res.data.address.station_id});
                                }
                            }
                        } else {
                            if (res.station_id) {
                                local.save(key, {station_id: res.station_id})
                            }
                        }
                        commit('setReady', 1);
                    }
                });
            } else {
                let {station_id, fake_station_id} = local.load(key);
                if (station_id || fake_station_id) {
                    commit('setReady', 1);
                    return;
                }

                let longitude = 0;
                let latitude = 0;
                if (platform.isWeixin) {
                    let {data} = await weixin.getLocation();
                    if (data) {
                        ({longitude, latitude} = data);
                    }
                }

                const {data} = await fetch.get(API.userLocationApi('check'), {lng: longitude, lat: latitude}, false);
                if (data) {
                    let {fake_station_info, around_addresses} = data;

                    if (fake_station_info) {
                        let {fake_station_id} = local.load(key);
                        if (!fake_station_id) {
                            local.update(key, {fake_station_id: fake_station_info.id});
                        }
                    }

                    if (around_addresses && around_addresses.station_id) {
                        local.update(key, {station_id: around_addresses.station_id})
                    }
                }

                commit('setReady', 1);
            }
        }
    }
};

const mutations = {
    setReady(state, data) {
        state.ready = data;
    }
};

export default {
    namespaced: true,
    state,
    actions,
    mutations
}