import qs from 'qs';

/**
 * 压缩七牛图片
 * @param url
 * @param size
 * @returns {string}
 */
const imgZip = (url, size) => {
    let new_url = '';
    if (!size) {
        size = 640;
    }
    if (url) {
        new_url = url.replace('!maicai.product.list', '');
    }
    return new_url ? (new_url + '?imageMogr2/thumbnail/' + size + 'x' + size) : ''
};

/**
 * 页面地址
 * @param path 路由
 * @param param 传参
 * @returns {string}
 */
const pageUrl = (path, param) => {
    let baseUrl = `${window.location.origin}/#${path}`;
    if (param) {
        baseUrl = `${baseUrl}?${qs.stringify(param)}`
    }
    return baseUrl;
};

/**
 * 判断是否是对象
 * @param val
 * @returns {boolean}
 */
const isObject = (val) => {
    return Object.prototype.toString.call(val) === "[object Object]"
};

/**
 * 判断是否是数组
 * @param val
 * @returns {boolean}
 */
const isArray = (val) => {
    return Object.prototype.toString.call(val) === "[object Array]"
};

/**
 * 获取url参数 @param string
 **/
const getQueryString = (name, url=null) => {
    const reg = new RegExp("(^|&|)" + name + "=([^&]*)(&|$)");
    const r = (url?url:window.location.href).substr(1).match(reg);
    if (r != null) return unescape(r[2]);
    return null;
};

/**
 * 添加更改url参数
 */
const changeQueryString = (name, value, u) => {
    let url = u || window.location.href;
    let newUrl = "";
    let reg = new RegExp("(^|)" + name + "=([^&]*)(|$)");
    let tmp = name + "=" + value;
    if (url.match(reg) != null) {
        newUrl = url.replace(eval(reg), tmp);
    } else {
        if (url.indexOf('?')>=0) {
            newUrl = url + "&" + tmp;
        } else {
            newUrl = url + "?" + tmp;
        }
    }
    return newUrl;
};

/**
 * @module compVer
 * @instance
 * @param {String} ver1 - 版本号1，格式 0.0.0
 * @param {String} ver2 - 版本号2，格式 0.0.0
 * @return {int} 1: ver1 > ver2, 0: ver1 = ver2, -1: ver1 < ver2
 * @desc 比较版本号
 */
const compVer = (ver1, ver2) => {
    let ver1periods = String(ver1).split('.'),
        ver2periods = String(ver2).split('.');
    let comp = function (a, b) {
        a = (a === undefined ? 0 : parseInt(a));
        b = (b === undefined ? 0 : parseInt(b));
        if (a > b) {
            return 1;
        }
        if (a == b) {
            return 0;
        }
        if (a < b) {
            return -1;
        }
    };

    let r0 = comp(ver1periods[0], ver2periods[0]),
        r1 = comp(ver1periods[1], ver2periods[1]),
        r2 = comp(ver1periods[2], ver2periods[2]);

    if (r0 == 0) {
        if (r1 == 0) {
            return r2;
        }
        return r1;
    }
    return r0;
};

export {
    imgZip,
    pageUrl,
    isObject,
    isArray,
    getQueryString,
    changeQueryString,
    compVer,
};